.portfolio-block__header{
	font-weight: 400;
	display: flex;
	justify-content: center;
	font-size: 26px;
}

.portfolio-block-wrapper{
	display: flex;
	flex-direction: column;
	align-items: center;
}

.portfolio-block-card{
	display: flex;
	flex: 0 1 35%;
	flex-direction: column;
	align-items: center;
	margin: 30px 20px;
	box-shadow: 0 0 50px #c4c4c4;
	border-radius: 20px 20px 20px 20px;
	max-width: 720px;
	min-width: 720px;

	@media(max-width:768px){
		max-width: 100%;
		min-width: 90%;
	}
}

.portfolio-block-card__img{
	border-radius: 20px 20px 0 0;
	min-width: 100%;

	@media(max-width:768px){
		width: 100%;
		height: auto;
	}
}

.portfolio-block-card__info{
	border-radius: 0 0 20px 20px;
	background-color: #f2f2f2;
	padding: 20px 30px 40px 30px;
	max-width: 100%;
	min-width: 100%;

	@media(max-width:576px){
		padding: 10px 15px 20px 15px;
	}
}

.card__info-main{
	display: flex;
	justify-content: space-between;
	align-items: inherit;
}

.card__info-tags{
	display: flex;
	font-weight: 400;
	font-size: 18px;
	font-weight: 500;

	@media(max-width:576px){
		font-size: 10px;
	}
}

.card__info-tag:not(:last-child){
	margin-right: 5px;
}

.card__info-date{
	margin-top: -22px;
	margin-bottom: 23px;
	font-size: 13px;

	@media(max-width:576px){
		font-size: 8px;
	}
}

.card__info-description{
	@media(max-width:576px){
		font-size: 10px;
	}
}