.header-block{
	padding: 40px 135px 60px 135px;

	@media(max-width:992px){
		padding: 20px 20px 60px 20px;
	}
}

.header-block__nav-wrapper{
	display: flex;
	justify-content: space-between;
}

.nav-wrapper__lang-switch{
	display: flex;
	font-size: 19px;

	@media(max-width:768px){
		font-size: 14px;
	}
}

.lang-switch__RU, .lang-switch__EN{
	padding-bottom: 5px;
	border-bottom: 3px solid transparent;
	text-decoration: none;
	cursor: pointer;
	transition: linear 0.4s;

	&:hover, &:focus {
		border-bottom: 3px solid #3e83a2;
		text-decoration: none;
	}

	@media(max-width:576px){
		font-size: 10px;
	}
}

.nav-wrapper__nav{
	display: flex;
	font-size: 19px;
}

.nav-menu-item{
	padding-bottom: 5px;
	border-bottom: 3px solid transparent;
	text-decoration: none;
	cursor: pointer;
	transition: linear 0.4s;

	&:hover, &:focus{
		border-bottom: 3px solid #3e83a2;
		text-decoration: none;
	}

	@media(max-width:768px){
		font-size: 14px;
	}

	@media(max-width:576px){
		font-size: 10px;
	}
}

.nav-menu-item:not(:last-child){
	margin-right: 20px;

	@media(max-width:576px){
		margin-right: 10px;
	}
}

.header-block__personal-info{
	padding: 40px 0 0 0;
	display: flex;
	align-items: center;
	justify-content: center;

	@media(max-width:768px){
		flex-direction: column;
	}
}

.personal-info__name-wrapper{
	padding: 20px 0;
}

.personal-info__name{
	font-weight: 500;
	font-size: 24px;
}

.header-block__socials{
	display: flex;
	justify-content: center;

	@media(max-width:768px){
		margin-top: 20px;
	}
}

a:not(:last-child){
	margin-right: 17px;
}

.social-icon{
	transition: linear 0.2s;
	&:hover{
		transform: scale(1.2);
	}
}

.personal-info__image{
	margin: 0 50px;
	box-shadow: 0 0 50px #c4c4c4;
	border-radius: 150px 150px;
}