.info-block{
	margin: 0 auto;
	max-width: 720px;

	@media(max-width:768px){
		padding: 0 20px;
	}
}

.info-block__inner-wrapper{
	display: flex;
	justify-content: space-around;

	background-color: white;
	border-radius: 20px 20px;
	padding: 40px 20px;
	box-shadow: 0 0 50px #c4c4c4;

	@media(max-width:768px){
		justify-content: space-between;
	}
}

.invisible{
	display: none;
}

.info-block__header{
	font-weight: 400;
	padding-bottom: 30px;
	text-align: center;
	font-size: 26px;
}

.info-block__list-title{
	font-weight: 500;

	@media(max-width:576px){
		font-size: 12px;
	}
}

.info-block__list-wrapper:not(:last-child){
	margin-right: 100px;

	@media(max-width:768px){
		margin-right: 40px;
	}
}

li {
	list-style-type: none;
	margin: 5px 0;
	font-weight: 300;

	@media(max-width:576px){
		font-size: 10px;
	}
}

ul{
	margin-top: 20px;
}


